import React, {Component} from "react";
import SmartMenuList from "./SmartMenuList";

class MobileMenu extends Component{
    constructor(props){
        super(props);
    }
    
    render(){
        return(
            <nav className="menu">
               <SmartMenuList 
                    menu={this.props.menu}
                /> 
               
                <style jsx>{`
                    .menu{
                        margin: 0px;
                        padding: 0;
                        background-color: black;
                        height: 40px;
                        opacity: 0.7;
                    }
                `}</style>
            </nav>
        );
    }

}

export default MobileMenu;